function renderStars(rating) {
  const totalStars = 5;
  return Array.from({ length: totalStars }).map((_, index) =>
    index < rating ? (
      <Star key={index} style={{ color: "#FFBF00" }} />
    ) : (
      <StarBorder key={index} style={{ color: "black" }} />
    )
  );
}

