import React, { useEffect, useRef } from "react";
import $ from "jquery";
import "tilt.js";
import "./counter.scss";
import MKTypography from "components/MKTypography";
import Grid from "@mui/material/Grid";
import MKBox from "components/MKBox";
import Container from "@mui/material/Container";

const Card = () => {
  const cardRefs = useRef([]);

  const cards = [
    {
      backgroundImage: "path/to/image1.jpg",
      title: "Card 1",
      description: "Description for Card 1",
    },
    {
      backgroundImage: "path/to/image2.jpg",
      title: "Card 2",
      description: "Description for Card 2",
    },
    {
      backgroundImage: "path/to/image3.jpg",
      title: "Card 3",
      description: "Description for Card 3",
    },
    {
      backgroundImage: "path/to/image3.jpg",
      title: "Card 3",
      description: "Description for Card 3",
    },
  ];

  useEffect(() => {
    cardRefs.current.forEach((cardElement) => {
      if (cardElement) {
        $(cardElement).tilt({
          maxTilt: 25,
          perspective: 1000,
          scale: 1.1,
          speed: 400,
          glare: true,
          "max-glare": 0.5,
        });
      }
    });

    // Cleanup on unmount
    return () => {
      cardRefs.current.forEach((cardElement) => {
        if (cardElement) $(cardElement).tilt.destroy();
      });
    };
  }, []);

  return (
    <MKBox component="section" py={3}>
      <Container>
        {/* PAGE STUFF */}
        <Grid
          container
          item
          xs={12}
          lg={6}
          justifyContent="center"
          sx={{ mx: "auto", textAlign: "center" }}
        >
          <MKTypography variant="h5" fontWeight="regular">
            Plongez dans l’élégance et la magie de votre mariage, immortalisé pour revivre à chaque
            instant la beauté de votre union
          </MKTypography>
        </Grid>
        <Grid
          container
          item
          xs={12}
          lg={6}
          justifyContent="center"
          sx={{ mx: "auto", textAlign: "center" }}
        >
          <MKTypography variant="h2">Nos réalisation</MKTypography>
        </Grid>
        <div className="card-list ">
          {cards.map((card, index) => (
            <div
              key={index}
              className="card page-counter"
              ref={(el) => (cardRefs.current[index] = el)}
              style={{ backgroundImage: `url(${card.backgroundImage})` }}
            >
              <div className="card__content">
                <a className="play-button">
                  <svg
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    xmlnsXlink="http://www.w3.org/1999/xlink"
                    viewBox="0 0 50 50"
                  >
                    <path
                      d="M42.7,42.7L25,50L7.3,42.7L0,25L7.3,7.3L25,0l17.7,7.3L50,25L42.7,42.7z"
                      className="polygon"
                    ></path>
                    <polygon points="32.5,25 21.5,31.4 21.5,18.6 "></polygon>
                  </svg>
                </a>
              </div>
            </div>
          ))}
        </div>
      </Container>
    </MKBox>
  );
};

export default Card;
